@import "../../scss/mixins";


.interviewsList {
    margin-bottom: 7.4rem;
  
}


.interviewsListGrid {
    display: grid;
    grid-template-columns: repeat(4, minmax(0, 1fr));
    grid-gap: 2rem;

    @include breakpoint(mobile) {
        grid-template-columns: repeat(2, minmax(0, 1fr));
    }
}